* {
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
}
*:before,
*:after {
  box-sizing: border-box;
}
html,
body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 14px;
  color: #2B3037;
  background-color: #E4E9F1;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
}
hr {
  border: unset;
  margin: 10px 0;
  border-bottom: solid 1px #d8dee7;
}
ul,
ol {
  list-style: none;
}
img,
label {
  width: inherit;
  user-select: none;
}
img {
  object-fit: cover;
  object-position: center;
}
button,
input,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  margin: 0;
  outline: 0;
}
input {
  padding: 0;
  width: 100%;
  border: unset;
  box-shadow: 0 0 0 transparent;
}
button {
  cursor: pointer;
  padding: 0;
  border: unset;
  box-shadow: 0 0 0 transparent;
  user-select: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  text-decoration: none;
  color: inherit;
  outline: 0;
  cursor: pointer;
  display: inline-block;
  user-select: none;
}
:root {
  --primary-color: #0A3068;
  --info-color: #2D8CF0;
  --success-color: #28B463;
  --warning-color: #FF9900;
  --error-color: #E53935;
  --yellow-color: #FFE500;
  --link-color: #005BEA;
  --text-color: #F0F8FF;
  --base-color: #E4E9F1;
  --gray-color: #757579;
  --bunker-gray: #131921;
  --shsak-gray: #2B3037;
  --border-color: #d8dee7;
  --font-size-larger: 22px;
  --font-size-large: 18px;
  --font-size-medium: 14px;
  --font-size-small: 12px;
  --font-size-smaller: 10px;
  --font-weight-lighter: 300;
  --font-weight-normal: 400;
  --font-weight-bold: 700;
  --font-weight-bolder: 900;
  --cubic-bezier: cubic-bezier(0.77, 0, 0.175, 1);
}
.ui--router-view {
  position: relative;
  min-height: 100vh;
}
.ui--loader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 128;
  padding: 20px;
  display: grid;
  transition: all 0.2s cubic-bezier(0.77, 0, 0.175, 1);
}
.ui--loader-container {
  position: relative;
  width: 45px;
  height: 45px;
  padding: 5px;
  margin: 0 auto;
  border-radius: 100%;
  background-color: #F0F8FF;
  box-shadow: 0 5px 15px rgba(255, 153, 0, 0.25);
  transition: all 0.2s ease;
}
.ui--loader .circle {
  animation: rotate 2s linear infinite;
  transform-origin: center center;
  width: 35px;
  height: 35px;
}
.ui--loader .path {
  stroke-dasharray: 1, 200;
  stroke-dashoffset: 0;
  stroke: #FF9900;
  animation: dash 1.5s ease infinite;
  stroke-linecap: round;
}
.ui--loader.loader-enter-active [class$="-container"],
.ui--loader.loader-leave-active [class$="-container"] {
  opacity: 1;
  transform: translate(0, 0);
}
.ui--loader.loader-enter [class$="-container"],
.ui--loader.loader-leave-to [class$="-container"] {
  opacity: 0;
  transform: translate(0, -10px);
}
@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes dash {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -35px;
  }
  100% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -124px;
  }
}
.ui--alert {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 130;
  padding: 20px;
  background-color: rgba(19, 25, 33, 0.6);
}
.ui--alert:not(.show) {
  opacity: 0;
  pointer-events: none;
}
.ui--alert-container {
  position: relative;
  overflow: hidden;
  margin: auto;
  max-width: 360px;
  color: #2B3037;
  background-color: white;
  border-radius: 4px;
  box-shadow: 0 10px 20px rgba(19, 25, 33, 0.4);
  transition: all 0.2s ease;
}
.ui--alert-container.slide-enter-active {
  opacity: 1;
  transform: translateY(0);
}
.ui--alert-container.slide-enter {
  opacity: 0;
  transform: translateY(-10px);
}
.ui--alert-title {
  padding: 0 20px;
  font-size: 16px;
  font-weight: 700;
  line-height: 46px;
  border-bottom: solid 1px #EDF3F3;
}
.ui--alert-content {
  position: relative;
  padding: 20px;
}
.ui--alert-footer {
  display: flex;
  justify-content: flex-end;
  padding: 10px 20px;
  border-top: solid 1px #EDF3F3;
}
.ui--alert-footer .btn {
  margin: 0 0 0 15px;
  width: 90px;
  height: 26px;
  font-size: 12px;
  font-weight: 700;
  text-align: center;
  text-transform: capitalize;
  border-radius: 2px;
}
.ui--input {
  position: relative;
}
.ui--input-label {
  color: #0A3068;
  line-height: 1;
  font-size: 14px;
  font-weight: 600;
  text-transform: capitalize;
}
.ui--input-label[required]::after {
  content: ' *';
  color: #E53935;
}
.ui--input-default {
  position: relative;
  display: block;
  margin: 5px 0 0;
  padding: 6px 15px;
  min-height: 40px;
  color: #2B3037;
  background-color: transparent;
  border-radius: 4px;
  background: #E4E9F1;
  box-shadow: inset 3px 3px 10px #CDD2D9, inset -3px -3px 10px #FBFFFF;
}
.ui--input-default:disabled {
  opacity: 0.9;
  cursor: not-allowed;
  user-select: none;
}
.ui--input-errors {
  color: #E53935;
  font-size: 12px;
  font-style: italic;
}
.ui--input.ltr {
  text-align: right;
}
.ui--input.ctr {
  text-align: center;
}
.btn {
  position: relative;
  display: block;
  width: 100%;
  height: 40px;
  margin: 0 auto;
  padding: 0 20px;
  font-size: 16px;
  font-weight: 600;
  border-radius: 4px;
}
.btn .text {
  padding: 0 15px;
  vertical-align: middle;
}
.btn .icon {
  vertical-align: middle;
  width: 18px;
  height: 18px;
}
.btn .icon.ltr {
  float: right;
}
.btn .icon.rtl {
  float: left;
}
.btn-default {
  color: #0A3068;
  background-color: #E4E9F1;
  box-shadow: 5px 5px 20px #C9CDD4, -5px -5px 20px #FFF;
}
.btn-primary {
  color: #F0F8FF;
  background-color: #0A3068;
  background-image: linear-gradient(145deg, #0B336F, #092B5E);
}
.btn-danger {
  color: #F0F8FF;
  background-color: #E53935;
}
.btn:active {
  opacity: 0.88;
}
.btn:disabled {
  opacity: 0.8;
  cursor: not-allowed;
  filter: grayscale(8%);
}
